<template>
  <div class="changeboat" >
    <el-drawer
      title="请选择您的小船"
      :visible.sync="drawer_flag"
      :direction="direction"
      :before-close="handleClose"
    >
      <hr/>
      <div style="height:70%;overflow:auto">
        <div v-for="item in boats" :key="item.id">
          <queryboat></queryboat>
          <hr/>
        </div>
      </div>
    </el-drawer>
  </div>
</template>

<script>
import QueryBoat from "./QueryBoat";
export default {
  data() {
    return {
      direction: "ltr",
      boats: [
        {
          id: 1,
          electricity: "67%",
          state: "离线",
          canrun: "3小时25分钟",
          positon: "杭州市西湖区西密湖"
        },{},{},{},{}
      ]
    };
  },
  components: {
    queryboat: QueryBoat
  },

  computed: {
    drawer_flag: {
      get() {
        return this.$store.state.drawer;
      },
      set(val) {
        this.$store.state.drawer = val;
      }
    }
  },

  mounted() {},

  methods: {
    handleClose(done) {
      this.$store.commit("closeDrawer");
      done();
    }
  }
};
</script>
